@import url(https://fonts.googleapis.com/css2?family=Vampiro+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Happy+Monkey&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans&display=swap);
html, body, #root, .App {
  margin: 0;
  height: 100%;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  margin: 0;
  height: 100%;
  background: #FEFBE6;

  display: flex;
  align-items: center;
  flex-direction: column
}

.header {
  width: 100%;
}

.arrow-back {
  position: fixed;
  top: 10px;
  left: 10px;
}

.logo {
  color: #000;
  outline: none;
  font-size: 96px;
  font-family: 'Vampiro One', cursive;

  text-align: center;
  top: 20px;
}

.F {
  color: #ffd900;
}

.subtitle p {
  font-size: 32px;
  font-family: 'Happy Monkey', cursive;

  text-align: center;
}

.container {
  width: 100%;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

@media screen and (max-width: 650px) {
  .logo {
    font-size: 32px;
  }

  .subtitle p {
    font-size: 24px;
  }
}

.search {
  width: 60%;
  margin: 10px;
  border-bottom: 2px solid #000;

  display: flex;
  align-items: center;
  justify-content: space-around;
}

input:focus {
  outline: none;
}

.input-search {
  width: 95%;

  background: transparent;
  border: transparent;
  font-size: 32px;
}

.cards-container {
  width: 80%;
  margin: 20px 0;

  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.card {
  width: 150px;
  height: 150px;
  border-radius: 25px;
  background: rgb(99, 99, 99);

  margin: 10px;

  display: flex;
  text-align: center;
  text-decoration: none;
  flex-direction: column;
  justify-content: center;

  -webkit-filter: drop-shadow(4px 4px 4px rgba(0, 0, 0, 0.25));

          filter: drop-shadow(4px 4px 4px rgba(0, 0, 0, 0.25));
}

.card:hover {
  width: 155px;
  height: 155px;

  transition: width .2s, height .2s;
}

.card p {
  color: #FFF;
  font-family: 'Nunito Sans', sans-serif;
  font-size: 14px;

  -webkit-user-select: none;

      -ms-user-select: none;

          user-select: none;
}

.create-button {
  position: fixed;
  bottom: 20px;
  right: 20px;

  z-index: 10;

  width: 64px;
  height: 64px;
  background: #ffd900;
  border: none;
  border-radius: 20px;

  display: flex;
  justify-content: center;
  align-items: center;

  transition: background-color 0.2s;
}

.create-button:hover {
  background: #f1cd00;
}

@media screen and (max-width: 650px) {
  .input-search {
    font-size: 16px;
  }

  .card p {
    font-size: 14px;
  }
}
.form {
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

span {
  white-space: pre-wrap;
}

.input-container {
  display: flex;
  flex-direction: column;

  margin-bottom: 20px;
}

label, button {
  font-size: 18px;
  font-family: 'Vampiro One', cursive;
}

input {
  height: 50px;
  margin-top: 5px;
  padding: 0 10px;

  font-size: 18px;

  border: none;
  border-radius: 20px;
  background-color: #FFF298;
}

textarea {
  height: 300px;
  padding: 10px 10px;

  outline: none;
  
  font-size: 18px;

  border: none;
  border-radius: 20px;
  background-color: #FFF298;
}

.save-button {
  height: 50px;
  border: none;
  background: #FFBF00;
  border-radius: 50px;

  transition: background-color .2s;

  cursor: pointer;
}

.save-button:hover {
  background: #EFB300;
}

@media screen and (max-width: 650px) {
  .form {
    width: 80%;
  }

  label {
    font-size: 14px;
  }

  input {
    font-size: 12px;
    height: 30px;
  }

  .card p {
    font-size: 14px;
  }
}
